.animation {
    background-color: #282c34;
    width: 100%;
    height: 100vh; 
    position: absolute;
    margin: 0;
    padding: 0;
    overflow: hidden;
    cursor: none;
    top: 0;
}

.cursor {
    width: 20px;
    height: 20px;
    border: 2px solid white;
    border-radius: 50%;
    position: absolute;
    pointer-events: none;
}

.box ul li {
    position: absolute;
    width: 25px;
    height: 25px;
    list-style: none;
    opacity: 0;
    background: radial-gradient(rgb(44, 79, 175), transparent, transparent);
    bottom : 0;
    left: 10vw;
    animation: animate 5s linear infinite;
}

.box ul li:nth-child(2) {
    left: 2vw;
    animation-delay: 1s;
}

.box ul li:nth-child(3) {
    left: 26vw;
    animation-delay: 2s;
}

.box ul li:nth-child(4) {
    left: 35vw;
    animation-delay: 4s;
}

.box ul li:nth-child(5) {
    left: 42vw;
    animation-delay: 3s;
}

.box ul li:nth-child(6) {
    left: 58vw;
    animation-delay: 5s;
}

.box ul li:nth-child(7) {
    left: 61vw;
    animation-delay: 2s;
}

.box ul li:nth-child(8) {
    left: 77vw;
    animation-delay: 3s;
}

.box ul li:nth-child(9) {
    left: 85vw;
    animation-delay: 1s;
}

@keyframes animate {
    0% {
        transform: scale(0);
        opacity: 1;
        bottom: 0;
    }
    100% {
        transform: scale(10);
        opacity: 0;
        bottom: 70vh;
    }
}
